import React from 'react';

class Images extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            imgSrc: ''
        };
        this.values = {'HS': '../public/logos/HS.png', 'ND': '../public/logos/ND.png', 'UB': '../public/logos/UB.png'};
    }
    
    render() {
        return (
            <div style="background-image: url('this.values[this.props.value]}')" />
        )
    }
}

export default Images;